<template>
    <div class="container">
        <h1 class="text-center my-5">App de Notas</h1>
        <b-alert
            :show="dismissCountDown"
            dismissible
            :variant="mensaje.color"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
        >
            {{ mensaje.texto }}
        </b-alert>
        <div class="row">
            <div class="col col-sm-12 col-md-4">
                <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
                    <div class="card border-warning">
                        <div class="card-header bg-warning">
                            <h3 class="text-center text-white">Editar nota</h3>
                        </div>
                        <div class="card-body">
                            <div class="form-group">
                                <label for="noteNameInput">* Nombre:</label>
                                <input type="text" class="form-control" id="noteNameInput" aria-describedby="nameHelp" v-model="notaEditar.name">
                                <small id="nameHelp" class="form-text text-info">* Campo obligatorio</small>
                            </div>
                            <div class="form-group">
                                <label for="noteDescription">Descripcion:</label>
                                <input type="text" class="form-control" id="noteDescription" v-model="notaEditar.description">
                            </div>
                            <div class="form-group text-right mt-3">
                                <b-button variant="warning" class="mr-2" type="submit">Editar <b-icon-pencil></b-icon-pencil></b-button>
                                <b-button variant="danger" type="submit" @click="editar = false">Cancelar <b-icon-slash-circle></b-icon-slash-circle></b-button>
                            </div>
                        </div>
                    </div>
                </form>
                <form @submit.prevent="agregarNota()" v-if="!editar">
                    <div class="card border-success">
                        <div class="card-header bg-success">
                            <h3 class="text-center text-white">Añadir una nota</h3>
                        </div>
                        <div class="card-body">
                            <div class="form-group">
                                <label for="noteNameInput">* Nombre:</label>
                                <input type="text" class="form-control" id="noteNameInput" aria-describedby="nameHelp" v-model="nota.name">
                                <small id="nameHelp" class="form-text text-info">* Campo obligatorio</small>
                            </div>
                            <div class="form-group">
                                <label for="noteDescription">Descripcion:</label>
                                <input type="text" class="form-control" id="noteDescription" v-model="nota.description">
                            </div>
                            <div class="form-group text-right">
                                <b-button variant="outline-success" size="lg" class="mt-3" type="submit">Agregar <b-icon-cursor></b-icon-cursor></b-button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
            <div class="col col-sm-12 col-md-8">
                <div class="table-responsive">                    
                    <table class="table table-striped table-bordered mt-5">
                        <thead class="text-white">
                          <tr class="text-center">
                            <th scope="col">Id</th>
                            <th scope="col">Nombre</th>
                            <th scope="col">Descripcion</th>
                            <th scope="col">Acciones</th>
                          </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in notas" :key="index">
                              <th scope="row"><span class="badge badge-primary">{{item._id}}</span></th>
                              <td>{{item.name}}</td>
                              <td>{{item.description}}</td>
                              <td>
                                  <div class="d-flex justify-content-around align-items-center">
                                       <b-button @click="activarEdicion(item._id)" variant="warning" class="mr-2 mr-md-0">
                                            <b-icon-pencil-square></b-icon-pencil-square>
                                        </b-button>
                                        <b-button @click="eliminarNota(item._id)" variant="danger">
                                            <b-icon-trash></b-icon-trash>
                                        </b-button>
                                  </div>
                              </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>        
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    data() {
        return {
            notas: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: '',
                texto: ''
            },
            nota: {
                name: '',
                description: ''
            },
            editar: false,
            notaEditar: {}
        }
    },
    created() {
        this.listarNotas();
    },
    methods: {
        listarNotas() {
            let config = {
                headers: {
                    token: this.token
                }
            }

            this.axios.get("/notes", config).then(res => {
                // console.log(res.data)
                this.notas = res.data
            }).catch(err => {
                console.error(err.response)
            })
        },
        agregarNota() {
            const config = {
                headers: {
                    token: this.token
                }
            }

            this.axios.post('/new-note', this.nota, config).then(res => {
                this.notas.push(res.data)
                this.nota.name = ''
                this.nota.description = ''
                this.mensaje.color = 'success'
                this.mensaje.texto = 'Nota agregada'
                this.showAlert()
            }).catch(err => {
                console.error(err.response)
                if (err.response.data.error.errors.name.message) {
                    this.mensaje.color = 'warning'
                    this.mensaje.texto = 'Se deben rellenar todos los campos requeridos'
                } else {
                    this.mensaje.color = 'danger'
                    this.mensaje.texto = 'Ocurrio un error en el sistema'
                }
                
                this.showAlert()
            })
        },
        eliminarNota(id){
            console.info(id)
            this.axios.delete(`/note/${id}`).then(res => {
                const index = this.notas.findIndex(item => item._id === res.data._id)
                this.notas.splice(index,1)
                this.mensaje.color = "warning";
                this.mensaje.texto = "Nota eliminada";
                this.showAlert();
            }).catch(err => {
                console.error(err.response)
            })
        },
        editarNota(obj) {
            this.axios.put(`/note/${obj._id}`, obj).then(res => {
                const index = this.notas.findIndex(item => item._id === res.data._id)
                this.notas[index].name = res.data.name
                this.notas[index].description = res.data.description
                this.mensaje.color = 'success'
                this.mensaje.texto = 'Nota editada'
                this.showAlert()
                this.editar = false
            }).catch(err => {
                console.error(err.response)
            })
        },
        activarEdicion(id) {
            this.editar = true
            console.info(id)
            this.axios.get(`/note/${id}`).then(res => {
                this.notaEditar = res.data
            }).catch(err => {
                console.error(err.response)
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        },
        alerta() {
            this.mensaje.color = "success";
            this.mensaje.texto = "Probando alerta";
            this.showAlert();
        }
    },
    computed: {
        ...mapState(['token'])
    }
}
</script>

<style scoped>
    h1 {
        letter-spacing: 2px;
        text-transform: uppercase;
        font-weight: 700;
        color: #13AA52;
    }

    thead {
        background: #13AA52;
    }
</style>