<template>
    <section class="container my-5 d-flex justify-content-center">
        <b-card class="border-dark p-5 form-card">
            <b-card-body>
                <h1 class="text-success text-center mb-5">Log In</h1>
                <form @submit.prevent="login">
                    <div class="alert alert-danger" v-if="message != ''" role="alert">
                        <b-icon-shield-lock-fill></b-icon-shield-lock-fill>  {{message}}
                    </div>
                    <div class="form group my-2">
                        <label for="input_email" class="text-muted">Email</label>
                        <input v-model="usuario.email" type="email" id="input_email" placeholder="example@example" class="form-control"> 
                    </div>
                    <div class="form-group my-2">
                        <label for="input_password" class="text-muted">Password</label>
                        <input v-model="usuario.password" type="password" id="input_password" placeholder="********" class="form-control">
                    </div>
                    <div class="form-group mt-5">
                        <hr>
                        <b-button class="btn btn-success btn-lg px-3 mt-4" type="submit">Acceder</b-button>
                    </div>
                </form>
            </b-card-body>
        </b-card>
    </section>
</template>
<script>
import { mapActions } from 'vuex'

export default {
    data() {
        return {
            usuario: {
                email: '',
                password: ''
            },
            message: ''
            
        }
    },
    methods: {
        login() {
            // console.log(this.usuario)
            this.axios.post('/login', this.usuario)
                      .then(res => {
                          console.log(res);
                          const token = res.data.token
                          this.saveUser(token)
                      })
                      .catch(err => {
                          console.error(err.response);
                          this.message = err.response.data.message
                      })
        },
        ...mapActions(['saveUser'])
    }
}
</script>

<style scoped>
    label,
    input::placeholder {
        letter-spacing: 1.3px;
    }

    .form-card {
        
        max-width: 500px;
    }
</style>